    orderCnt: orderCnt
  },
  watch: {
    $route: function $route(to, from) {
      // 对路由变化作出响应...
      this.status = this.$route.params.status ? Number(this.$route.params.status) : 1;
      this.activeName = this.activeName ? this.activeName : "1";

      if (to.params.status !== from.params.status) {
        //切换左边菜单清空搜索条件
        this.filterParams = JSON.parse(this.filterParamsDefault);
      }

      this.gotoPage();
      this.gettabList();
    },
    status: function status() {
      this.$refs.gridTable.clearSelection();
      this.filterParams = JSON.parse(this.filterParamsDefault);
      this.activeName = "1";
    },
    activeName: function activeName() {
      this.$refs.gridTable.clearSelection();
      this.toPage(1);
    }
  },
  mounted: function mounted() {
    var _this = this;

    if (this.$route.query.type) {
      this.activeName = String(this.$route.query.type);
    }

    this.getItem();
    this.dialogPayDefault = JSON.stringify(this.dialogPay);
    this.dialogCheckStockDefault = JSON.stringify(this.dialogCheckStock);
    var status = this.$route.params.status ? Number(this.$route.params.status) : 1;
    var paystatus = this.$route.query.paystatus ? Number(this.$route.query.paystatus) : 1;

    if (status == 2 && this.activeName == "1" && (paystatus == 2 || paystatus == 3)) {
      var returnPayId = localStorage.getItem("c_returnPayId") || "";
      var returnPaySessionId = localStorage.getItem("c_returnPaySessionId") || "";

      if (paystatus == 2) {
        this.paySuccess(returnPayId, returnPaySessionId);
      }

      localStorage.setItem("c_returnPayStatus", paystatus);
    }

    window.addEventListener('storage', function (e) {
      if (e.key == "returnPayStatus") {
        //支付回调，支付结果弹层
        if (_this.dialogPay.isShow) {
          var returnPayStatus = localStorage.getItem("c_returnPayStatus") || "";

          if (returnPayStatus == 2) {
            //支付成功
            _this.dialogPay.step = 3;

            _this.closePayPage();
          } else {
            //支付失败
            _this.dialogPay.step = 3;
            _this.dialogPay.msg = "Payment failed";

            _this.closePayPage();
          }
        }
      }
    });
    setInterval(function () {}, 1000);
  },
  beforeDestroy: function beforeDestroy() {},
  methods: {
    shippedFn: function shippedFn(row) {
      this.dialogLogistic.errorType = row.shippingMethodArr.errorType;

      if (row.shippingMethodArr.errorSkuList) {
        this.dialogLogistic.items = row.shippingMethodArr.errorSkuList;
      }

      this.dialogLogistic.max = row.shippingMethodArr.max;
      this.dialogLogistic.min = row.shippingMethodArr.min;
      this.dialogLogistic.isShow = true;
    },
    deleteOrder: function deleteOrder(i, data) {
      var _this2 = this;

      var ids = [];
      data.items.forEach(function (e) {
        if (e.check) {
          ids.push(e.id);
        }
      });

      if (ids.length == 0) {
        ids.push(i.id);
      }

      this.$confirm('确定删除该商品？', 'Tips', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: "warning"
      }).then(function () {
        _this2.$apiCall('api.ShopifyOrder.delshopifyItem', {
          ids: ids
        }, function (r) {
          if (r.ErrorCode == 9999) {
            _this2.getItem();

            _this2.$elementMessage('Delete Successful!', "success");
          } else {
            _this2.$elementMessage(r.Message, "error");
          }
        });
      }).catch(function () {
        _this2.$message({
          type: 'info',
          message: '已取消'
        });
      });
    },
    handlePurchaseChange: function handlePurchaseChange(selection) {
      var _this3 = this;

      this.abnormalSelection = [];
      selection.forEach(function (item) {
        _this3.abnormalSelection.push(item.id);
      });
    },
    ImportFn: function ImportFn() {
      this.createVisible = true;
    },
    ImportCancel: function ImportCancel() {
      this.createVisible = false;
    },
    createUpload: function createUpload() {
      var _this4 = this;

      if (!this.uploadfileList.length) {
        this.$elementMessage('Please choose file', "error");
        return;
      }

      if (this.uploadfileList[0].raw.size / 1024 / 1024 > 2) {
        this.$message({
          message: 'file cannot over 2MB',
          type: "error"
        });
        return;
      }

      var uploadFile = this.uploadfileList[0].raw;
      this.$showLoading();

      if ($("#ifileProCreate").length == 0) {
        $("body").append('<iframe id="ifileProCreate" style="display:none"></iframe>');
      }

      var openDownload = function openDownload(url) {
        document.getElementById("ifileProCreate").src = url;
      };

      var apiUrl = "api.ShopifyOrder.importOrderExcel";
      var param = {
        "@file": uploadFile
      };
      this.$apiCall(apiUrl, param, function (r) {
        if (r.ErrorCode == "9999") {
          _this4.$elementMessage('Create Success', "success");

          openDownload(r.Data.Results.file);

          _this4.getItem();

          _this4.createVisible = false;
          _this4.uploadfileList = [];
        } else {
          _this4.$elementMessage(r.Message, "error");

          if (r.ErrorCode == "1002" || r.ErrorCode == "1003") {
            _this4.$userFailure(_this4);
          }
        }

        _this4.$hideLoading();
      });
    },
    downloadFile: function downloadFile() {
      var url = "/newFile/orders_create.xlsx";

      if ($("#downloadFile").length == 0) {
        $("body").append('<iframe id="downloadFile" style="display:none"></iframe>');
      }

      var openDownload = function openDownload(url) {
        document.getElementById("downloadFile").src = url;
      };

      openDownload(url);
    },
    fileBeforeUpload: function fileBeforeUpload(file) {
      this.uploadfileList = [];
      this.uploadfileList.push(file);
    },
    abnormalClose: function abnormalClose() {
      this.batchChange = false;
      this.abnormalStatus = false;
      this.abnormalData = [];
    },
    AbnormalReClick: function AbnormalReClick() {
      var _this5 = this;

      if (!this.canPay && this.abnormalType == 1) {
        this.abnormalStatus = false;
        return;
      }

      this.abnormalBtnVisible = true;

      if (this.batchChange) {
        // 批量
        this.$apiCall('api.ShopifyOrder.reOfferOrPay', {
          ids: this.abnormalSelection,
          type: this.abnormalType
        }, function (r) {
          _this5.abnormalBtnVisible = false;
          _this5.abnormalStatus = false;

          if (r.ErrorCode == 9999) {
            if (r.Data.Results) {
              _this5.$elementMessage('Change Success', 'success');

              if (_this5.abnormalType == 1) {
                _this5.$router.push({
                  path: '/ordersManage/2'
                });
              } else if (_this5.abnormalType == 2) {
                _this5.$router.push({
                  path: '/ordersManage/1',
                  query: {
                    type: 2
                  }
                });
              }
            }
          }
        });
      } else {
        // 单个
        this.$apiCall('api.ShopifyOrder.reOfferOrPay', {
          ids: [this.details.id],
          type: this.abnormalType
        }, function (r) {
          _this5.abnormalBtnVisible = false;
          _this5.abnormalStatus = false;

          if (r.ErrorCode == 9999) {
            if (r.Data.Results) {
              _this5.$elementMessage('Change Success', 'success');

              if (_this5.abnormalType == 1) {
                _this5.$router.push({
                  path: '/ordersManage/2'
                });
              } else if (_this5.abnormalType == 2) {
                _this5.$router.push({
                  path: '/ordersManage/1',
                  query: {
                    type: 2
                  }
                });
              }
            }
          }
        });
      }
    },
    BatchContinuePay: function BatchContinuePay(type) {
      var _this6 = this;

      // 批量处理 购买 报价
      this.batchChange = true;
      this.abnormalType = type;
      this.$apiCall('api.ShopifyOrder.checkOfferOrPay', {
        ids: this.abnormalSelection,
        type: type
      }, function (r) {
        if (r.ErrorCode == 9999) {
          _this6.canPay = r.Data.Results.isPayFlag;
          _this6.abnormalData = r.Data.Results.abnormalSkus;

          if (_this6.abnormalData.length > 0) {
            _this6.abnormalStatus = true;
          } else if (_this6.abnormalData.length == 0 && _this6.abnormalType == 2) {
            _this6.$elementMessage('No goods need to be eliminated', 'error');
          } else if (_this6.abnormalData.length == 0 && _this6.abnormalType == 1) {
            _this6.AbnormalReClick();
          }
        } else {
          _this6.$elementMessage(r.Message, 'error');
        }
      });
    },
    AbnormalContinuePay: function AbnormalContinuePay(row, type) {
      var _this7 = this;

      this.abnormalType = type;
      this.details = row;
      this.$apiCall('api.ShopifyOrder.checkOfferOrPay', {
        ids: [row.id],
        type: this.abnormalType
      }, function (r) {
        if (r.ErrorCode == 9999) {
          _this7.canPay = r.Data.Results.isPayFlag;
          _this7.abnormalData = r.Data.Results.abnormalSkus;

          if (_this7.abnormalData.length > 0) {
            _this7.abnormalStatus = true;
          } else if (_this7.abnormalData.length == 0 && _this7.abnormalType == 2) {
            _this7.$elementMessage('No goods need to be eliminated', 'error');
          } else if (_this7.abnormalData.length == 0 && _this7.abnormalType == 1) {
            _this7.AbnormalReClick();
          }
        }
      });
    },
    show: function show(row) {
      var _this8 = this;

      this.$refs['popover' + row.orderId].showPopper = true;
      this.$nextTick(function () {
        _this8.$refs['popover' + row.orderId].updatePopper();
      });
    },
    enter: function enter(row, column, cell) {
      this.show(row); // this.$refs['popover'+row.orderId].showPopper = true
