    updateCountDown: function updateCountDown() {
      var _this14 = this;

      var TIME_COUNT = 60;

      if (!this.timer) {
        this.count = TIME_COUNT;
        this.isDisabled = true;
        this.timer = setInterval(function () {
          if (_this14.count > 0 && _this14.count <= TIME_COUNT) {
            _this14.count--;
