    countDown: function countDown(item) {
      var _this15 = this;

      var time = item.timeCreated;
      var timeLeft = "";
      item.clock = setInterval(function () {
        var start = moment.unix(time).format("YYYY-MM-DD HH:mm:ss"); //获取开始时间

        var end = moment.unix(time).add(2, 'days').format('YYYY-MM-DD HH:mm:ss'); //结束时间

        var today = moment().format('YYYY-MM-DD HH:mm:ss'); //今天时间

        var diff = moment(end).diff(moment(today)); //时间差

        if (diff > 0) {
          if (moment.duration(diff).days() > 0) {
            timeLeft = "".concat(moment.duration(diff).hours() + 24, " : ").concat(moment.duration(diff).minutes(), " : ").concat(moment.duration(diff).seconds()); //格式化为需要的格式 这里是时分秒	
          } else {
            timeLeft = "".concat(moment.duration(diff).hours(), " : ").concat(moment.duration(diff).minutes(), " : ").concat(moment.duration(diff).seconds()); //格式化为需要的格式 这里是时分秒	
          }
        } else {
          timeLeft = "null";
          clearInterval(item.clock);
        }

        _this15.$set(item, "timeLeft", timeLeft);
      }, 1000);
    },
