        this.$elementMessage("Please select an order first", 'error');
      } else {
        this.$confirm("Are you sure to abandon the quotation for the selected order? After giving up, it cannot be restored, please operate with caution", "", {
          confirmButtonText: "Confirm",
          cancelButtonText: "Cancel",
          type: ''
        }).then(function () {
          _this25.$showLoading();

          _this25.$apiCall("api.ShopifyOrder.cancel", {
            ids: _this25.checkIds
          }, function (r) {
            if (r.ErrorCode == "9999") {
              _this25.$elementMessage("Successfully", "success");

              _this25.getItem();
            } else {
              _this25.$elementMessage(r.Message, "error");
            }

            _this25.$hideLoading();
          });
        }).catch(function () {});
      }
    },
    // val表示选中的表格行数据
    handleSelectionChange: function handleSelectionChange(val) {
      var _this26 = this;

      this.checkIds = [];
      this.checkItems = [];
      if (!val.length) return;
      val.forEach(function (item) {
        _this26.checkItems.push(item);

        _this26.checkIds.push(item.id);
      });
    },
    dateChange: function dateChange() {
      if (this.filterParams.date && this.filterParams.date.length > 1) {
        this.filterParams.dateFrom = moment(this.filterParams.date[0]).format("YYYY-MM-DD");
        this.filterParams.dateTo = moment(this.filterParams.date[1]).format("YYYY-MM-DD");
        this.filterItem();
      } else {
        this.clearFilter('date');
      }
    },
    dateChange2: function dateChange2() {
      if (this.filterParams.date2 && this.filterParams.date2.length > 1) {
        this.filterParams.dateFrom2 = moment(this.filterParams.date2[0]).format("YYYY-MM-DD");
        this.filterParams.dateTo2 = moment(this.filterParams.date2[1]).format("YYYY-MM-DD");
        this.filterItem();
      } else {
        this.clearFilter('date2');
      }
    },
    clearFilter: function clearFilter(type) {
      switch (type) {
        case "codeName":
          this.filterParams.codeName = "";
          break;

        case "orderId":
          this.filterParams.orderId = "";
          break;

        case "receiverName":
          this.filterParams.receiverName = "";
          break;

        case "accountIds":
          this.filterParams.accountIds = "";
          break;

        case "date":
          this.filterParams.date = [];
          this.filterParams.dateFrom = "";
          this.filterParams.dateTo = "";
          break;

        case "date2":
          this.filterParams.date2 = [];
          this.filterParams.dateFrom2 = "";
          this.filterParams.dateTo2 = "";
          break;

        case "sysCode":
          this.filterParams.sysCode = "";
          break;

        default:
          this.filterParams = JSON.parse(this.filterParamsDefault);
          break;
      }

      this.filterItem();
    },
    filterItem: function filterItem(type) {
      if (this.$route.query.page == 1) {
        this.getItem();
      } else {
        this.$router.push({
          path: "/ordersManage/".concat(Number(this.status)),
          query: {
            page: 1
          }
        });
      }
    },
    getStore: function getStore() {
      var _this27 = this;

      this.$apiCall("api.Invoice.findStores", {}, function (r) {
        if (r.ErrorCode == 9999) {
          _this27.storeList = r.Data.Results;
        } else {
          _this27.$message({
            message: r.Message,
            type: "error"
          });
        }
      });
    },
    gettabList: function gettabList() {
      switch (this.status) {
        case 1:
          this.tabList = [{
            id: 1,
            name: 1,
            title: "Quotation to be submitted"
          }, {
            id: 2,
            name: 2,
            title: "Vendor quoted order"
          }];
          break;

        case 2:
          this.tabList = [{
            id: 3,
            name: 1,
            title: "Pending payment"
          }, {
            id: 22,
            name: 2,
            title: "Payment to be completed"
          }, {
            id: 23,
            name: 3,
            title: "Payment has not arrived"
          }];
          break;

        case 3:
          this.tabList = [{
            id: 5,
            name: 1,
            title: "Waiting for fulfillment"
          }, {
            id: 6,
            name: 2,
            title: "Fulfilled"
          }];
          break;

        case 4:
          this.tabList = [{
            id: 8,
            name: 1,
            title: "Logistics synchronization failed"
          }, {
            id: 9,
            name: 2,
            title: "Unable to purchase"
          }, {
            id: 10,
            name: 3,
            title: "Products Off shelf"
          }];
          break;

        default:
          this.tabList = [];
          break;
      }
    },
    getCustomerAllOrderCnt: function getCustomerAllOrderCnt() {
      var _this28 = this;

      this.$apiCall("api.ShopifyOrder.getCustomerAllOrderCnt", {}, function (r) {
        if (r.ErrorCode == 9999) {
          _this28.customerAllOrderCnt = r.Data.Results;
        } else {
          _this28.$message({
            message: r.Message,
            type: "error"
          });
        }
      });
    },
    customerAllOrderCntText: function customerAllOrderCntText(id) {
      var text = 0;

      if (id == 22) {
        text = this.customerAllOrderCnt.waitCustomerPayCnt ? this.customerAllOrderCnt.waitCustomerPayCnt : 0;
      } else if (id == 23) {
        text = this.customerAllOrderCnt.waitVendorConfirmCnt ? this.customerAllOrderCnt.waitVendorConfirmCnt : 0;
      } else {
        text = this.customerAllOrderCnt[id] ? this.customerAllOrderCnt[id] : 0;
      }

      return text;
    },
    getItem: function getItem(s) {
      var _this29 = this;

      this.getStore();
      this.gettabList();
      this.getCustomerAllOrderCnt();
      this.items = [];
      var status = "",
          url = "api.ShopifyOrder.shopifyOrders",
          params,
          params1 = {
        page: this.page,
        rowsPerPage: this.rowsPerPage,
        status: status,
        timeCreatedFrom: this.filterParams.dateFrom,
        timeCreatedTo: this.filterParams.dateTo,
        accountIds: [this.filterParams.accountIds],
        orderId: this.filterParams.orderId,
        keywords: this.filterParams.codeName,
        receiverName: this.filterParams.receiverName
      },
          params2 = {
        page: this.page,
        rowsPerPage: this.rowsPerPage,
        status: status,
        sysCode: this.filterParams.sysCode,
        dateFrom: this.filterParams.dateFrom2,
        dateTo: this.filterParams.dateTo2
      };
      params = params1;

      if (this.status == 5) {
        //已完成
        status = 7;
      } else if (this.status == 6) {
        //查询页面
        status = "";
        params = Object.assign(params1, params2);
        params.isAll = true;
      } else {
        var item = this.tabList.filter(function (item) {
          return item.name == _this29.activeName;
        });
        status = item[0].id;

        if (this.status == 2) {
          //付款
          if (this.activeName == 2 || this.activeName == 3) {
            status = this.activeName;
            params = params2;
            url = "api.ShopifyOrder.findByInvoice";
          }
        }
      }

      this.loading = true;
      params.status = status;
      this.$apiCall(url, params, function (r) {
        _this29.loading = false;

        if (r.ErrorCode == 9999) {
          _this29.items = r.Data.Results;

          _this29.items.forEach(function (item) {
            if (_this29.status == 2 && _this29.activeName != 1) {
              _this29.countDown(item);
            }

            if (item.shippingMethodArr && item.shippingMethodArr.length) {
              _this29.$set(item, "shippingMethodItem", item.shippingMethodArr[0]);

              _this29.$set(item, "shippingMethodId", item.shippingMethodArr[0].id);

              _this29.$set(item, "shippingMethodFee", Number(item.shippingMethodArr[0].fee));
            }

            _this29.$set(item, "shippingJsonInfo", item.shippingJson ? JSON.parse(item.shippingJson) : "");

            if (item.items.length) {
              item.items.forEach(function (obj) {
                _this29.$set(obj, "totalPrice", Number(obj.vendorPrice) * Number(obj.quantity));
              }); // if(item.shippingMethodItem){//有运费加上运费
              // 	this.$set(item,"totalAllAmount",this.countTotal(item.items,'totalPrice') + Number(item.shippingMethodItem.fee));
              // }else{
              // 	this.$set(item,"totalAllAmount",this.countTotal(item.items,'totalPrice'));
              // }
            } // else{
            // 	if(item.shippingMethodItem){//有运费加上运费
            // 		this.$set(item,"totalAllAmount",Number(item.shippingMethodItem.fee));
            // 	}else{
            // 		this.$set(item,"totalAllAmount",0);
            // 	}
            // }


            if (item.shippingMethodItem) {
              //有运费加上运费
              _this29.$set(item, "totalAllAmount", (item.itemAmount ? Number(item.itemAmount) : 0) + Number(item.shippingMethodItem.fee));
            } else {
              _this29.$set(item, "totalAllAmount", item.itemAmount ? Number(item.itemAmount) : 0);
            }
          });

          _this29.total = Number(r.Data.Pagination.totalCount);
          _this29.totalPage = Number(r.Data.Pagination.totalPage);

          _this29.$getTableHeight(_this29);

          if (!s) _this29.$root.$children[0].$refs.mainScroll.wrap.scrollTop = 0;
        } else {
          _this29.$message({
            message: r.Message,
            type: "error"
          });
        }
      });
    },
    countTotal: function countTotal(arr, keyName) {
      var $total = 0;
      $total = arr.reduce(function (total, currentValue, currentIndex, arr) {
        return currentValue[keyName] ? total + currentValue[keyName] : total;
      }, 0);
      return $total;
    },
    toPage: function toPage(val) {
      if (val != this.$route.query.page) this.$router.push({
        path: "/ordersManage/".concat(Number(this.status)),
        query: {
          page: val
        }
      });
    },
    changePageSize: function changePageSize(val) {
      this.rowsPerPage = val;
      localStorage.setItem("c_ordersRowsPerPage", val);
      this.getItem();
    },
    gotoPage: function gotoPage() {
      this.page = this.$route.query.page ? Number(this.$route.query.page) : 1;
      this.getItem();
    },
    handleClick: function handleClick(tab, event) {
      this.filterParams = JSON.parse(this.filterParamsDefault);
      this.getItem();
    }
  }
};